/* MailChimp Form Embed Code - Classic - 12/17/2015 v10.7 */
form {
    display:block;
    position:relative;
    text-align:left;
    padding:10px 0 10px 3%;
}
h2 {
    font-weight:bold;
    padding:0;
    margin:15px 0;
    font-size:1.4em;
}
input {
    border: 1px solid #ABB0B2;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
}

input[type=checkbox]{
    -webkit-appearance:checkbox;
}
input[type=radio]{
   -webkit-appearance:radio;
}

input:focus {
    border-color:#333;

}

