 .small-meta {
     font-size: 11px;
}

 .nowrap {
     white-space:nowrap;
}


 .mc-field-group {
     clear:left;
    position:relative;
    width:96%;
    padding-bottom:3%;
     min-height:50px;
}

 .size1of2 {
    clear: none;
    // float: left;
    display:inline-block;
    width:46%;
     margin-right:4%;
}

* html  .size1of2 {
    margin-right:2%;

    /* Fix for IE6 double margins. */
}

 .mc-field-group label {
     display:block;
     margin-bottom:3px;
}

 .mc-field-group input {
     display:block; width:100%;
     padding:8px 0;
     text-indent:2%;}

 .mc-field-group select {
     display:inline-block;
    width:99%;
    padding:5px 0;
     margin-bottom:2px;
}


 .datefield,
 .phonefield-us{
     padding:5px 0;
}

 .datefield input,
 .phonefield-us input{
     display:inline; width:60px;
     margin:0 2px;
     letter-spacing:1px;
     text-align:center;
     padding:5px 0 2px 0;
}

 .phonefield-us,
  .phonearea input,
 .phonefield-us,
 .phonedetail1 input{
     width:40px;
}

 .datefield,
 .monthfield input,
 .datefield,
 .dayfield input{
     width:30px;
}

 .datefield label,
 .phonefield-us label{
     display:none;
}


 .indicates-required {
     text-align:right;
     font-size:11px;
     margin-right:4%;
}

 .asterisk {
     color:#e85c41;
     font-size:150%;
     font-weight:normal;
     position:relative; top:5px;
}

 .clear {
     clear:both;
}


 .mc-field-group,
 .input-group ul {
     margin:0;
     padding:5px 0;
     list-style:none;
}

 .mc-field-group,
 .input-group ul li {
     display:block;
     padding:3px 0;
     margin:0;
}


 .mc-field-group,

 .input-group label {
     display:inline;
}

 .mc-field-group,
 .input-group input {
     display:inline;
     width:auto;
     border:none;
}


 div#mce-responses {
    float:left;
    top:-1.4em;
    padding:0em .5em 0em .5em;
    overflow:hidden;
    width:90%;
    margin: 0 5%;
    clear: both;

}

div.response {
    margin:1em 0;
    padding:1em .5em .5em 0;
    font-weight:bold;
    float:left;
    top:-1.5em;
    z-index:1;
    width:80%;
}

#mce-error-response {
    display:none;
}

#mce-success-response {
    color:#529214;
    display:none;
}

label.error {
    display:block;
    float:none;
    width:auto;
    margin-left:1.05em;
    text-align:left;
    padding:.5em 0;
}


#mc-embedded-subscribe {
    clear:both;
    width:auto;
    display:block;
    margin:1em 0 1em 5%;
}

#num-subscribers {
    font-size:1.1em;
}

#num-subscribers span {
    padding:.5em;
    border:1px solid #ccc;
    margin-right:.5em;
    font-weight:bold;
}


#mc-embedded-subscribe-form div,
.mce_inline_error {
    display:inline-block;
    margin:2px 0 1em 0;
    padding:5px 10px;
    background-color:rgba(255,255,255,0.85);
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    font-size:14px;
    font-weight:normal;
    z-index:1;
    color:#e85c41;
}

#mc-embedded-subscribe-form input,
.mce_inline_error {
    border:2px solid #e85c41;
}
